import React from 'react';
import ReactDOM from 'react-dom';

import { Provider } from 'mobx-react';
import pokemon from "./Stores/pokemon";
import Poket from './App/App';
import PoketInfo from './App/pokemonInfo';
import {HashRouter, Route } from "react-router-dom";
import './style/main.css';

const stores = { pokemon };

ReactDOM.render(
   
        <Provider  { ...stores }>
              <HashRouter>
                <Route path="/"  component={Poket} />
                <Route path="/:pokemonName" exact component={PoketInfo} />
             </HashRouter>
        </Provider>,

     document.getElementById('root')
);