import React from 'react';
import '../style/pokemon.css';
import { observer, inject } from "mobx-react";

const Poke = inject('pokemon')(observer(
    class Pokemon extends React.Component {

        handleClick = () => {
            let stat = [];
    
            for (var i = 0; i < 6; i++) {
                stat.push(
                    <div className='pokemonStats' key={this.props.prop[2][i].stat.url}>
                        {this.props.prop[2][i].stat.name} - {this.props.prop[2][i].base_stat}
                    </div>
                );
            }
    
            for (var j = 0; j < this.props.prop[3].length; j++) {
                stat.push(
                    <div className='poketSlot' key={this.props.prop[3][j].type.url}>
                        {this.props.prop[3][j].type.name}
                    </div>
                );
            }
            const temp =
                <div className="poketInfo">
                    <div className='pokemonPages'>
                        <div className='pokemonImg'>
                            <span>{this.props.prop[1]}</span>
                            <img src={this.props.prop[0]} alt={this.props.prop[1]}></img>
                        </div>
                        <div className='pokemonInformation'>
                            {stat}
                        </div>
                    </div>
                    <div className='gridBack'>
                        <button onClick={this.handleBack} className='buttonBack'>back</button>
                    </div>
                </div>;
            this.props.pokemon.changeTempPokemon(temp);//new array
            this.props.pokemon.clickPokemon(true);
    
        }
        handleBack = () => {
            this.props.pokemon.clickPokemon(false);
        }
        handlePush = () => {
            this.temp =
                <div className="Pokemon" onClick={this.handleClick} key={this.props.prop[1]}>
                    <img src={this.props.prop[0]} alt='pokemon' />
                    <span>{this.props.prop[1]}</span>
                </div>;
            localStorage.setItem(JSON.stringify(this.props.prop[1]), JSON.stringify(this.props.prop));
        }
    
        componentWillMount() {
            this.handlePush();
        }
    
        render() {
            return (
                <div>
                    {this.temp}
                </div>
            );
        }
    }
))

export default Poke;