import React from 'react';
import '../style/pokemon.css';
import Pokemon from './pokemon';
import { observer, inject } from "mobx-react";
import { compose } from 'recompose';

window.onbeforeunload = () => {
    localStorage.setItem('firstLoad', 'false');
}

let requestRoot = "https://pokeapi.co/api/v2/pokemon/";

const PokeBeta = inject('pokemon')(observer(
    class Pokedex extends React.Component {

        componentWillMount() {
            let page = +this.props.match.params.numberPage;
            console.log('beta', page);
            this.props.pokemon.changeAll();
            this.getPokemon((this.props.pokemon.sizeView * page) - this.props.pokemon.sizeView + 1, page * this.props.pokemon.sizeView);

        }

        handlePush = (props) => {
            let tempGrid = [];
            tempGrid.push(<Pokemon prop={props} key={props[1]} click={this.props.history} />)
            this.props.pokemon.pushArray(tempGrid);
            localStorage.setItem(JSON.stringify(props[1]), JSON.stringify(props));
        }

        getPokemon = (a, b) => {
            console.log('get pokemon', a, b);
            for (var i = a; i <= b; i++) {
                const request = `${requestRoot}${i}/`;
                fetch(request, {
                    cache: "force-cache"
                })
                    .then(response => response.json())
                    .then(data => {
                        const speciesRequest = data.species.url;
                        let temp = [];
                        temp.push(data.sprites['front_default'], data.name, data.stats, data.types, data.weight);
                        this.props.pokemon.pushPokemon(temp);
                        this.handlePush(temp);
                        return fetch(speciesRequest);
                    })
            }
        }
        render() {
            return (
                <div >
                    {this.props.pokemon.clickOnPokemon ? this.props.pokemon.tempPokemon : <div className="FlexGrid" >{this.props.pokemon.array}</div>}
                </div>

            );
        }
    }
))

export default PokeBeta